(function (doc, win) {
    var docEl = doc.documentElement;

    // 一物理像素在不同屏幕的显示效果不一样。要根据devicePixelRatio来修改meta标签的scale
    (function () {
        var dpr = 1;
        var scale = 1;
        var isIPhone = win.navigator.appVersion.match(/iphone/gi);
        var devicePixelRatio = win.devicePixelRatio;

        if (isIPhone) {
            // iOS下，对于2和3的屏，用2倍的方案，其余的用1倍方案
            if (devicePixelRatio >= 3) {
                dpr = 3;
            } else if (devicePixelRatio >= 2) {
                dpr = 2;
            }
        }
        scale = 1 / dpr;
        var metaEl = '';
        metaEl = doc.createElement('meta');
        metaEl.setAttribute('name', 'viewport');
        metaEl.setAttribute('content', 'width=device-width,initial-scale=' + scale + ',user-scalable=no');
        if (docEl.firstElementChild) {
            docEl.firstElementChild.appendChild(metaEl);
            console.log(docEl.firstElementChild);
        } else {
            var wrap = doc.createElement('div');
            wrap.appendChild(metaEl);
            doc.write(wrap.innerHTML);
        }
        // let divElBasement = doc.createElement('div');
        // let divElCover = doc.createElement('div');
        // divElBasement.setAttribute('id', 'basement');
        // divElCover.setAttribute('id', 'cover');
        // let bodyEl = doc.body;
        // bodyEl.appendChild(divElBasement);
        // bodyEl.appendChild(divElCover);
    })();

    var isMobile = navigator.userAgent.match(/ipad|iphone|midp|rv:1.2.3.4|ucweb|android|windows ce|windows mobile|micromessenger/i);
    var resizeEvt = 'orientationchange' in window ? 'orientationchange' : 'resize';
    var clientWidth = docEl.clientWidth; // 此处放在外层防止input唤起页面重新计算font-size
    var clientHeight = docEl.clientHeight;
    var min = Math.min(clientWidth, clientHeight);
    var recalc = function recalc() {
        /* eslint-enable */
        if (isMobile) {
            // 移动端按750标准（以iphone6标准开发，iphone6上1rem == 100px）设置字体
            var fontSize = Math.floor(min / 75) * 10;
            docEl.style.fontSize = fontSize + 'px';
        } else {
            // 此处为兼容pc端手动调整的，不影响手机上效果(pc上1px任然为1px)
            docEl.style.fontSize = '100px';
        }
        // 页面横竖屏切换时让页面向上滚动到顶部，解决fixed定位浮动bug
        window.scrollTo(0, 0);
    };
    // 如果浏览器不支持addEventListener方法，终止运行
    if (!doc.addEventListener) return;
    win.addEventListener(resizeEvt, recalc, false); // 页面调试(横竖屏)时重新设置字体
    doc.addEventListener('DOMContentLoaded', recalc, false); // 页面dom结构加载完设置字体
})(document, window);

